body
    background-color: #E5D4CD
.header 
    background-color: #653F4C
    display: flex
    justify-content: space-between
    align-items: center
    position: relative
    width: 100%
    height: 73px
    flex-wrap: nowrap
    padding: 0 10px 0 10px
    color: #E5D4CD
    
.menu-header li
    display: inline-block
    padding: 50px
a 
    text-decoration: none
    color: #E5D4CD
    font-family: Manrope
    font-size: 18px
    font-style: normal
    font-weight: 400
.home-page__title 
    font-size: 6vh
    font-family: "Crimson Pro"
    font-weight: 700
    color: #653F4C
    margin-bottom: 0
.home-page
    display: flex
.home-page__text-area
    display: flex
    flex-direction: column
    align-items: center
.home-page__images
    display: block
    position: -1







